.block--u .quote-carousel {
  position: relative;
  max-width: 700px;
  margin: auto;
  margin-bottom: 65px !important;
}
.block--u .quote-carousel .flex-direction-nav a {
  opacity: 1 !important;
}
.block--u .quote-carousel .flex-prev,
.block--u .quote-carousel .flex-next {
  width: 47px;
  height: 44px;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("/wp-content/themes/custom_theme/assets/media/icons/slider.svg");
}
.block--u .quote-carousel .flex-prev:hover, .block--u .quote-carousel .flex-prev:focus,
.block--u .quote-carousel .flex-next:hover,
.block--u .quote-carousel .flex-next:focus {
  background-image: url("/wp-content/themes/custom_theme/assets/media/icons/slider-hover.svg");
}
.block--u .quote-carousel .flex-next {
  transform: rotate(180deg);
}
.block--u .quote-carousel .lSSlideOuter .lSPager.lSpg > li {
  padding: 0 10px;
}
.block--u .quote-carousel .lSSlideOuter .lSPager.lSpg > li a {
  height: 18px;
  width: 18px;
}
.block--u .quote-carousel .slider-navigation {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media screen and (min-width: 1025px) {
  .block--u .quote-carousel .slider-navigation {
    transform: translateX(-10%);
    width: 123%;
    top: 40%;
  }
}
.block--u .quote-carousel .lSAction > a {
  opacity: 1;
}
.block--u .quote-carousel .slide {
  border-radius: 12px;
  background-color: #002ea6;
  padding: 24px;
  max-width: 700px;
}
@media screen and (min-width: 1025px) {
  .block--u .quote-carousel .slide {
    padding: 42px;
  }
}
@media screen and (max-width: 767px) {
  .block--u .quote-carousel .slide {
    min-height: 360px;
  }
}
.block--u .quote-carousel .slide .quote-img-wrap {
  display: none !important;
}
.block--u .quote-carousel .slide .quote-by-info {
  padding-left: 22px !important;
  font-family: 600;
}
.block--u .quote-carousel .slide .quote-by-info strong,
.block--u .quote-carousel .slide .quote-by-info b {
  font-weight: 600;
}
.block--u .quote-carousel .slide .quote-by-info::before {
  background-color: #fff;
}
.block--u .quote-carousel .slide .company-name {
  font-family: "Libre Franklin", sans-serif;
}
.block--u .quote-carousel .slide .person-img-wrap {
  display: flex;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  margin-bottom: 15px;
}
.block--u .quote-carousel .slide .person-img-wrap .star--rating {
  padding-left: 15px;
}
.block--u .quote-carousel .slide .person-img-wrap img {
  border-radius: 50%;
  border: 2px solid #17fcc4;
  width: 56px;
  height: 56px;
}
.block--u .quote-carousel .slide .txt-content,
.block--u .quote-carousel .slide .txt-content p {
  color: #fff;
}
.block--u .quote-carousel .lSSlideOuter {
  overflow: visible;
}
.block--u .quote-carousel .lSSlideWrapper {
  border-radius: 12px;
  box-shadow: 3px 7px 7.5px 0px rgba(0, 0, 0, 0.1490196078);
}
.block--u .quote-carousel .slide {
  background-color: #fff;
}
.block--u .quote-carousel .slide .person-img-wrap .star--rating {
  display: none;
}
.block--u .quote-carousel .lSPager {
  display: none;
}
.block--u .quote-carousel .slide .txt-content {
  color: #002ea6;
}
.block--u .quote-carousel .slide .txt-content p {
  color: #002ea6;
}
.block--u .quote-carousel .slide .quote-by-txt,
.block--u .quote-carousel .slide .quote-by-info {
  position: relative;
  padding-left: 30px;
}
.block--u .quote-carousel .slide .quote-by-txt::before {
  content: "";
  position: absolute;
  left: -5px;
  top: 0px;
  height: 30px;
  width: 30px;
  background: url("/wp-content/themes/custom_theme/assets/media/icons/quote-left-mark-red.png") no-repeat center center;
}
.block--u .quote-carousel .slide .person-img-wrap img {
  border: none;
}
.block--u .quote-carousel .slider-navigation a {
  position: relative;
  height: 45px;
  width: 45px;
  background: #939598;
  border-radius: 50%;
  transform: none;
  transition: all 0.3s ease;
}
.block--u .quote-carousel .slider-navigation a:hover, .block--u .quote-carousel .slider-navigation a:focus {
  background: #002ea6;
}
.block--u .quote-carousel .slider-navigation a::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 15px;
  height: 15px;
  border-right-color: #fff;
  border-top-color: #fff;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  pointer-events: none;
  backface-visibility: hidden;
  transition: transform 0.3s cubic-bezier(0.61, 1, 0.88, 1);
  transform: translate3D(-50%, -50%, 0);
}
.block--u .quote-carousel .slider-navigation a.flex-prev::before {
  left: calc(50% + 4px);
  transform: translate3D(-50%, -50%, 0) rotate(45deg);
}
.block--u .quote-carousel .slider-navigation a.flex-next::before {
  left: calc(50% - 4px);
  transform: translate3D(-50%, -50%, 0) rotate(-135deg);
}
@media screen and (max-width: 1024px) {
  .block--u {
    padding-top: 48px !important;
  }
  .block--u .slider-navigation {
    display: flex;
    position: absolute;
    justify-content: space-between;
    width: 112%;
    top: 50%;
    left: -5%;
    transform: translateY(-50%);
  }
}
@media screen and (min-width: 1025px) {
  .block--u .slider-navigation {
    width: 125%;
  }
}
.block--u.two-columns .txt-content {
  display: flex;
}
.block--u.two-columns .txt-content > .col-sm-12 {
  flex-shrink: 1;
  margin-right: 10.486vw;
}
.block--u.quotes-light .quote-carousel {
  max-width: 425px;
}